define('mvctree/router', ['exports', 'ember', 'mvctree/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route("about");
  });

  exports['default'] = Router;

});